<template>
    <div>
        <v-row class="my-10">
            <v-col col="12" offset-sm="1" sm="10" order-sm="1">
                <v-card class="pa-4">
                    <v-card-actions class="px-4">
                        <v-card-title>
                            <v-icon left>admin_panel_settings</v-icon>Administración de productos
                        </v-card-title>
                        <v-spacer></v-spacer>
                        <v-text-field label="buscar" v-model="search"></v-text-field>
                        <v-spacer></v-spacer>
                        <v-btn color="primary" @click="createProductState=true">AGREGAR PRODUCTO</v-btn>
                    </v-card-actions>
                    <v-divider></v-divider>
                    <v-row class="justify-center align-center">
                        <v-col col="12" sm="2">
                            <v-card-title class="justify-center">Imagen</v-card-title>
                        </v-col>
                        <v-col col="12" sm="2">
                            <v-card-title class="justify-center">titulo</v-card-title>
                        </v-col>
                        <v-col col="12" sm="2">
                            <v-card-title class="justify-center">descripción</v-card-title>
                        </v-col>
                        <v-col col="12" sm="2">
                            <v-card-title class="justify-center">categoría</v-card-title>
                        </v-col>
                        <v-col col="12" sm="1">
                            <v-card-title class="justify-center">precio</v-card-title>
                        </v-col>
                        <v-col col="12" sm="3">
                            <v-card-title class="justify-center">acciones</v-card-title>
                        </v-col>
                    </v-row>
                    <v-divider></v-divider>

                    <div v-if="createProductState" class="my-4">
                        <v-row class="justify-center align-center">
                            <v-col col="12" sm="2">
                                <v-img v-if="newProductImagePath != ''" :src="newProductImagePath" class="pointer" @click="changeNewProductImage">
                                <v-icon right>edit</v-icon>
                                </v-img>
                                <v-btn v-else raised class="primary" @click="changeNewProductImage">Subir imagen</v-btn>
                                <input 
                                    type="file" 
                                    style="display: none" 
                                    ref="newInputFile" 
                                    accept="image/*"
                                    @change="onChangeNewInputFile">
                            </v-col>
                            <v-col col="12" sm="2">
                                <v-text-field v-model="newProductTitle"></v-text-field>
                            </v-col>
                            <v-col col="12" sm="2">
                                <v-text-field v-model="newProductDesctiption"></v-text-field>
                            </v-col>
                            <v-col col="12" sm="2">
                                <v-text-field v-model="newProductCategory"></v-text-field>
                            </v-col>
                            <v-col col="12" sm="2">
                                <v-text-field v-model="newProductPrice">$</v-text-field>
                            </v-col>
                            <v-col col="12" sm="1">
                                <v-btn block color="success" @click="createProduct" :loading="saveNewProductLoading">Guardar</v-btn>
                            </v-col>
                            <v-col col="12" sm="1">
                                <v-btn block color="error" @click="cancelCreateProduct">Cancelar</v-btn>
                            </v-col>
                        </v-row>
                    </div>

                    <div v-for="(product, index) in products" v-bind:key="product.title" class="my-4">
                        <v-row v-if="product.title.includes(search)" class="justify-center align-center">
                            <v-col col="12" sm="2">
                                <v-img v-if="!editing" aspect-ratio="1" :src="product.image"></v-img>
                                <div v-else-if="product.edit">
                                    <v-btn v-if="imageUrl == ''" raised class="primary" @click="onPickFile">Subir imagen</v-btn>
                                    <v-img v-else class="pointer" :src="imageUrl" aspect-ratio="1" @click="onPickFile">
                                    <v-icon right>
                                        edit
                                    </v-icon>
                                    </v-img>
                                    <input 
                                        type="file" 
                                        style="display: none" 
                                        ref="fileInput" 
                                        accept="image/*"
                                        @change="onFilePicked">
                                </div>
                                <v-img v-else aspect-ratio="1" :src="product.image"></v-img>
                            </v-col>
                            <v-col col="12" sm="2">
                                <v-card-text class="text-center">{{product.title}}</v-card-text>
                            </v-col>
                            <v-col col="12" sm="2">
                                <v-card-text v-if="!editing" class="text-center">{{product.description}}</v-card-text>
                                <v-text-field v-else-if="product.edit" v-model="description"></v-text-field>
                                <v-card-text v-else class="text-center">{{product.title}}</v-card-text>
                            </v-col>
                            <v-col col="12" sm="2">
                                <v-card-text v-if="!editing" class="text-center">{{product.category}}</v-card-text>
                                <v-text-field v-else-if="product.edit" v-model="category"></v-text-field>
                                <v-card-text v-else class="text-center">{{product.title}}</v-card-text>
                            </v-col>
                            <v-col col="12" sm="1">
                                <v-card-text v-if="!editing" class="text-center">${{product.price}}</v-card-text>
                                <v-text-field v-else-if="product.edit" v-model="price">$</v-text-field>
                                <v-card-text v-else class="text-center">{{product.title}}</v-card-text>
                            </v-col>
                            <v-col col="12" sm="3">
                                <v-row class="justify-center align-center">
                                    <v-col col="12" offset-sm="1" sm="4" order-sm="1">
                                        <v-btn v-if="!editing" color="blue" block @click="editProduct(index)">Editar</v-btn>
                                        <v-btn v-else-if="product.edit" color="success" @click="editProduct(index)" :loading="editLoading" block>Guardar</v-btn>
                                        <v-btn v-else color="warning" block>Esperar</v-btn>
                                    </v-col>
                                    <v-col col="12" offset-sm="1" sm="4" order-sm="1">
                                        <v-btn v-if="!editing" color="error" block @click="deletePetition(product.title)">Eliminar</v-btn>
                                        <v-btn v-else-if="product.edit" color="error" @click="()=>{editing = !editing; product.edit = !product.edit}" block>Cancelar</v-btn>
                                        <v-btn v-else color="error" block @click="deletePetition(product.title)">Eliminar</v-btn>
                                    </v-col>
                                </v-row>
                            </v-col>
                        </v-row>
                        <v-card-actions>
                            <v-divider></v-divider>
                        </v-card-actions>
                    </div>
                </v-card>
            </v-col>
        </v-row>
        <v-dialog v-model="dialog" width="50%">
            <v-card>
        <v-card-title class="justify-center">
          ¿Está seguro que quiere eliminar el producto?
        </v-card-title>

        <v-divider></v-divider>

        <v-card-actions>
          <v-btn
            color="primary"
            @click="dialog = !dialog"
            width="40%"
          >
            NO
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            color="error"
            @click="deleteProduct"
            width="40%"
            :loading="deleteLoading"
          >
            SI
          </v-btn>
        </v-card-actions>
      </v-card>
        </v-dialog>
    </div>
</template>

<script>
import { mapMutations } from 'vuex'
import productProvider from '../providers/ProductProvider'

export default {
    name:'AdminProductView',
    data(){
        return{
            deleteLoading: false,
            saveNewProductLoading: false,
            newProductImagePath: '',
            editLoading: false,
            createProductState: false,
            newProductImage: '',
            newProductTitle: '',
            newProductDesctiption: '',
            newProductCategory: '',
            newProductPrice: '',
            titleToDelete: '',
            dialog: false,
            editing: false,
            products: [],
            image: '',
            imageUrl: '',
            title: '',
            description: '',
            category: '',
            price: '',
            search: ''
        }
    },
    async created(){
        const {data: {getProducts: products}} = await productProvider.fetchProducts()

        products.forEach((_, i) => {
            products[i].edit = false
        })
        this.products = products.reverse()
    },
    methods:{
        ...mapMutations(['setAlert']),
        onChangeNewInputFile(event){
            const files = event.target.files
            let filename = files[0].name
            if(filename.lastIndexOf('.') <= 0){
                return this.setAlert({
                    state: true,
                    text: 'Archivo invalido',
                    type: 'error'
                })
            }
            const fileReader = new FileReader()
            fileReader.addEventListener('load', ()=> {
                this.newProductImagePath = fileReader.result
                console.log(this.image)
            })
            fileReader.readAsDataURL(files[0])
            this.newProductImage = files[0]
        },
        changeNewProductImage(){
            this.$refs.newInputFile.click()
        },
        onPickFile(){
            this.$refs.fileInput[0].click()
        },
        onFilePicked(event){
            const files = event.target.files
            let filename = files[0].name
            if(filename.lastIndexOf('.') <= 0){
                return this.setAlert({
                    state: true,
                    text: 'Archivo invalido',
                    type: 'error'
                })
            }
            const fileReader = new FileReader()
            fileReader.addEventListener('load', ()=> {
                this.imageUrl = fileReader.result
                console.log(this.image)
            })
            fileReader.readAsDataURL(files[0])
            this.image = files[0]
        },
        async createProduct(){
            this.saveNewProductLoading = true
            try {
                let url = ''
                const resp = await productProvider.uploadImage(this.newProductImage)
                if(!resp) throw new Error('no existe data')
                url = resp.data
                const {data: {createProduct}} = await productProvider.createProduct(
                    this.$store.state.userModule.user.token,
                    url,
                    this.newProductTitle,
                    this.newProductDesctiption,
                    this.newProductPrice,
                    this.newProductCategory
                )
                this.setAlert({
                    state: true,
                    text: 'Se creó un nuevo producto exitosamente.',
                    type: 'success'
                })
                //SOFT CREATE
                this.products.unshift(createProduct)
                this.cancelCreateProduct(true)
            } catch (error) {
                this.setAlert({
                    state: true,
                    text: 'Ocurrió un problema al crear un nuevo producto.',
                    type: 'error'
                })
                console.log(error)
            }
            this.saveNewProductLoading = false
        },
        cancelCreateProduct(value){
            this.newProductImage = ''
            this.newProductTitle = ''
            this.newProductDesctiption = ''
            this.newProductCategory = ''
            this.newProductPrice = ''
            this.createProductState = false
            if(value) return
            this.setAlert({
                state: true,
                text: 'Se cancelo la creación de un nuevo producto.',
                type: 'warning'
            })
        },
        deletePetition(title){
            this.titleToDelete = title
            this.dialog = true
        },
        async deleteProduct(){
            this.deleteLoading = true
            try {
                await productProvider.deleteProduct(this.$store.state.userModule.user.token, this.titleToDelete)
                this.setAlert({
                    state: true,
                    text: 'Se ha eliminado una publicación.',
                    type: 'success'
                })
                this.products.map((e, i)=>{
                    if(e.title === this.titleToDelete) this.products.splice(i, 1)
                })
            } catch (error) {
                this.setAlert({
                    state: true,
                    text: 'Ocurrió un problema al eliminar la publicación',
                    type: 'error'
                })
                console.log(error)
            }
            this.dialog = false
            this.deleteLoading = false
        },
        async editProduct(index){
            this.editLoading = true
            if(this.editing){
                try {
                    let url = ''
                    const resp = await productProvider.uploadImage(this.image)
                    if(!resp) url = this.image
                    else url = resp.data
                    await productProvider.updateProducts(
                        this.$store.state.userModule.user.token,
                        url,
                        this.title,
                        this.description,
                        this.price,
                        this.category
                    )
                    this.products[index].image = this.imageUrl
                    this.products[index].title = this.title
                    this.products[index].description = this.description
                    this.products[index].price = this.price
                    this.products[index].category = this.category
                    this.setAlert({
                        state: true,
                        text: 'Se modificó con exito un producto.',
                        type: 'success'
                    })
                    this.editing = false
                } catch (error) {
                    this.setAlert({
                        state: true,
                        text: 'Ocurrió un error al modificar el producto.',
                        type: 'error'
                    })
                    console.log(error)
                }

            }else{
                this.imageUrl = this.products[index].image
                this.title = this.products[index].title
                this.description = this.products[index].description
                this.category = this.products[index].category
                this.price = this.products[index].price
                this.editing = true
                this.products[index].edit = true
            }
            this.editLoading = false
        }
    }
}
</script>

<style scoped>
    .pointer{
        cursor: pointer;
    }
</style>