<template>
  <div class="my-10">
    <v-card width="80vw" class="mx-auto pa-4">
        <v-row class="justify-center align-center">
            <v-col cols="12" sm="12" md="6">
                <v-text-field label="categoría" v-model="category"></v-text-field>
            </v-col>
            <v-col cols="12" sm="12" md="6">
                <v-text-field label="buscar" v-model="search"></v-text-field>
            </v-col>
            <v-col cols="12">
                <v-btn block color="primary" @click="fetchProducts">BUSCAR</v-btn>
            </v-col>
        </v-row>
        <v-divider></v-divider>
        <v-row class="justify-center align-center">
            <v-col cols="12" sm="12" md="12" lg="4" xl="4" v-for="product of products" v-bind:key="product.title">
                <v-card class="pa-4">
                    <v-card-title>{{product.title}}</v-card-title>
                    <v-img aspect-ratio="1" :src="product.image"></v-img>
                    <v-row class="justify-center align-center">
                        <v-col cols="8" sm="8" md="8" lg="8" xl="8">
                            <v-card-text>{{product.description}}</v-card-text>
                        </v-col>
                        <v-col cols="4" sm="4" md="4" lg="4" xl="4">
                            <v-card-text class="success--text text-h5">${{product.price}}</v-card-text>
                        </v-col>
                    </v-row>
                    <v-divider></v-divider>
                    <v-card-actions>
                        <v-btn
                        color="primary"
                        class="ma-2 white--text"
                        @click="loader = 'loading3'"
                        >
                        Añadir al carrito
                        <v-icon
                            right
                            dark
                        >
                        shopping_cart
                        </v-icon>
                        </v-btn>
                        <v-spacer></v-spacer>
                        <v-btn
                        color="success"
                        class="ma-2 white--text"
                        @click="consultWhatsapp(product.title, product.price)"
                        >
                        whatsapp
                        <v-icon
                            right
                            dark
                        >
                        monetization_on
                        </v-icon>
                        </v-btn>
                    </v-card-actions>
                        <v-row>
                            <v-col cols="6">
                            </v-col>
                            <v-col  cols="6">
                            </v-col>
                        </v-row>
                </v-card>
            </v-col>
        </v-row>
        <v-row>
            <v-btn v-if="page>1" plain @click="changePage(page-1)">{{'< página anterior'}}</v-btn>
            <v-spacer></v-spacer>            
            <v-btn v-if="existProxPage" plain @click="changePage(page+1)">{{'próxima página >'}}</v-btn>
        </v-row>
    </v-card>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import productProvider from '../providers/ProductProvider'
export default {
    data(){
        return{
            products: [],
            search: '',
            category: 'ALIMENTO',
            page: 1,
            existProxPage: false,
        }
    },
    created(){
        this.fetchProducts()
    },
    mounted(){
        this.fetchProducts()
    },
    methods:{
        ...mapMutations(['setAlert']),
        consultWhatsapp(title, price){
            window.open(`whatsapp://send?text=Buenas!! Quería hacerte consultar por el producto ${title} con un valor de $${price}...&phone=+5491153326667`)
        },
        changePage(page){
            this.page = page
            this.fetchProducts()
        },
        async fetchProducts(){
            try{
                if(this.$vuetify.breakpoint.name == "xs" || this.$vuetify.breakpoint.name == "sm" || this.$vuetify.breakpoint.name == "md"){
                    const {data: {getFilterProducts: products}} = await productProvider.fetchProductsFilter(this.category, this.search, 0, 0)
                    this.products = products
                }
                else{
                    const {data: {getFilterProducts: products}} = await productProvider.fetchProductsFilter(this.category, this.search, this.page, 3)
                    this.products = products
                    const {data: {getFilterProducts: proxProducts}} = await productProvider.fetchProductsFilter(this.category, this.search, this.page + 1, 3)
                    console.log(proxProducts.length)
                    if(proxProducts.length==0) this.existProxPage = false
                    else this.existProxPage = true
                }
            }catch(error){
                this.setAlert({
                    state: true,
                    text: 'Ocurrió un problema al cargar los productos.',
                    type: 'error'
                })
                console.log(error)
            }   
        }
    }
}
</script>

